//Import the CSS
import './nanocubes.css';


//My classes
import Nanocube from './src/Nanocube/Nanocube4';
import Viewer from './src/Nanocube/Viewer';


//https://stackoverflow.com/questions/8486099/how-do-i-parse-a-url-query-parameters-in-javascript
function getArgFromUrl() {
    let query = window.location.search.substr(1);
    let result = {};
    query.split("&").forEach(function(part) {
        var item = part.split("=");
        if(item[0] !== ""){
            result[item[0]] = decodeURIComponent(item[1]);
        }
    });
        
    return result;
}


function startViewer(config,urlargs){
    $.when.apply($, Object.keys(config.nanocube).map(function(k){
        return Nanocube.initNanocube(config.nanocube[k].url);
    })).done(function(){
        let nchash = {};
        let ncnames = Object.keys(config.nanocube);
        let args = Array.prototype.slice.call(arguments);
        args.forEach(function(d,i){
            nchash[ncnames[i]] = d;
        });
        let viewer = new Viewer({
            nanocubes: nchash,
            div_id:'#nc',
            config: config,
            urlargs: urlargs
        });
        viewer.update();
    }).fail(function(){
        console.log('failed to parse' + urlargs.config);
    });
}

//main
let urlargs = getArgFromUrl();
urlargs.config = urlargs.config || './config.json';

$.getJSON(urlargs.config).done(function(config){
    startViewer(config,urlargs);
}).fail(function(err){
    console.log("failed to parse json"+ err);
});
    
